@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
:root{
  --button: 40px;
  --button-hover: 45px;
}

*{
  margin: 0;
  padding: 0;
  outline: 0;
  border: none;
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}

@-webkit-keyframes gettingBigger{
  0%{
      width: 40px;
      width: var(--button);
      height: 40px;
      height: var(--button);
  }

  100%{
      width: 45px;
      width: var(--button-hover);
      height: 45px;
      height: var(--button-hover);
  }
}

@keyframes gettingBigger{
  0%{
      width: 40px;
      width: var(--button);
      height: 40px;
      height: var(--button);
  }

  100%{
      width: 45px;
      width: var(--button-hover);
      height: 45px;
      height: var(--button-hover);
  }
}

button{
  width: 40px;
  width: var(--button);
  height: 40px;
  height: var(--button);
}

button:hover{
  -webkit-animation: gettingBigger .3s;
          animation: gettingBigger .3s;
  width: 45px;
  width: var(--button-hover);
  height: 45px;
  height: var(--button-hover);
}

@media screen and (max-width: 900px){
  :root{
    --button: 30px;
    --button-hover: 35px;
  }
}

